import React, { useState, useEffect, useMemo } from 'react';
import { Menu, X, Code2, User, Settings, LogOut } from 'lucide-react';
import './Header.scss';

export const Header: React.FC = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);
  const [activeSection, setActiveSection] = useState('components'); // Default to components

  const navigationItems = useMemo(() => [
    { label: 'Components', href: '#components', id: 'components' },
    { label: 'Integrations', href: '#drag-drop', id: 'drag-drop' },
    { label: 'Tabs', href: '#tabs', id: 'tabs' },
    { label: 'Tables', href: '#tables', id: 'tables' },
    { label: 'About', href: '#about', id: 'about' },
  ], []);

  // Scroll detection to highlight active section
  useEffect(() => {
    const handleScroll = () => {
      const sections = navigationItems.map(item => item.id);
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight;
      const scrollPosition = window.scrollY;
      const headerOffset = 80; // Header height offset
      
      // Check if we're at the bottom of the page
      if (scrollPosition + windowHeight >= documentHeight - 50) {
        setActiveSection('about');
        return;
      }
      
      // Check if we're at the top of the page (within first 200px)
      if (scrollPosition < 200) {
        setActiveSection('components');
        return;
      }
      
      // Calculate visible area for each section
      let maxVisibleArea = 0;
      let mostVisibleSection = 'components';
      
      for (const sectionId of sections) {
        const element = document.getElementById(sectionId);
        if (element) {
          const rect = element.getBoundingClientRect();
          
          // Calculate the visible portion of this section
          const viewportTop = headerOffset; // Account for header
          const viewportBottom = windowHeight;
          
          // Find the intersection between section and viewport
          const visibleTop = Math.max(rect.top, viewportTop);
          const visibleBottom = Math.min(rect.bottom, viewportBottom);
          
          // Calculate visible height (0 if section is not visible)
          const visibleHeight = Math.max(0, visibleBottom - visibleTop);
          
          // Calculate visible area as percentage of viewport
          const visibleArea = visibleHeight / (viewportBottom - viewportTop);
          
          // Only consider sections that are actually visible
          if (visibleHeight > 0 && visibleArea > maxVisibleArea) {
            maxVisibleArea = visibleArea;
            mostVisibleSection = sectionId;
          }
        }
      }
      
      // Update active section based on visibility (require >50% visibility)
      if (maxVisibleArea > 0.5) {
        setActiveSection(mostVisibleSection);
      } else {
        // Fallback to "Components" when no section has >50% visibility
        // This prevents selecting tabs for barely visible sections
        setActiveSection('components');
      }
    };

    // Set initial active section
    handleScroll();
    
    // Add scroll listener
    window.addEventListener('scroll', handleScroll);
    
    // Cleanup
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [navigationItems]);

  const handleLogin = () => {
    setIsLoggedIn(true);
    setIsUserMenuOpen(false);
  };

  const handleLogout = () => {
    setIsLoggedIn(false);
    setIsUserMenuOpen(false);
  };

  const toggleUserMenu = () => {
    setIsUserMenuOpen(!isUserMenuOpen);
  };

  return (
    <header className="header">
      <div className="container">
        <div className="header__content">
          {/* Logo */}
          <div className="header__logo">
            <div className="header__logo-icon">
              <Code2 size={24} />
            </div>
            <span className="header__logo-text">
              Beyond<span className="text-gradient-purple">Components</span>
            </span>
          </div>

          {/* Desktop Navigation */}
          <nav className="header__nav">
            <ul className="header__nav-list">
              {navigationItems.map((item) => (
                <li key={item.href}>
                  <a 
                    href={item.href} 
                    className={`header__nav-link ${activeSection === item.id ? 'header__nav-link--active' : ''}`}
                  >
                    {item.label}
                  </a>
                </li>
              ))}
            </ul>
          </nav>

          {/* Theme Toggle & Auth */}
          <div className="header__actions">
            
            {!isLoggedIn ? (
              <button className="btn-primary" onClick={handleLogin}>
                Sign In
              </button>
            ) : (
              <div className="header__user-menu">
                <button 
                  className="header__user-button"
                  onClick={toggleUserMenu}
                  aria-label="User menu"
                >
                  <div className="header__user-avatar">
                    <User size={16} />
                  </div>
                  <span className="header__user-name">John Doe</span>
                </button>
                
                {isUserMenuOpen && (
                  <div className="header__user-dropdown">
                    <div className="header__user-info">
                      <div className="header__user-avatar header__user-avatar--large">
                        <User size={20} />
                      </div>
                      <div>
                        <div className="header__user-name-large">John Doe</div>
                        <div className="header__user-email">john.doe@company.com</div>
                      </div>
                    </div>
                    <div className="header__user-actions">
                      <button className="header__user-action">
                        <Settings size={16} />
                        User Settings
                      </button>
                      <button className="header__user-action" onClick={handleLogout}>
                        <LogOut size={16} />
                        Sign Out
                      </button>
                    </div>
                  </div>
                )}
              </div>
            )}
          </div>

          {/* Mobile Menu Button */}
          <button
            className="header__mobile-toggle"
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
            aria-label="Toggle mobile menu"
          >
            {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isMobileMenuOpen && (
          <div className="header__mobile-menu">
            <nav className="header__mobile-nav">
              <ul className="header__mobile-nav-list">
                {navigationItems.map((item) => (
                  <li key={item.href}>
                    <a
                      href={item.href}
                      className={`header__mobile-nav-link ${activeSection === item.id ? 'header__mobile-nav-link--active' : ''}`}
                      onClick={() => setIsMobileMenuOpen(false)}
                    >
                      {item.label}
                    </a>
                  </li>
                ))}
              </ul>
              <div className="header__mobile-actions">
                <a href="#contact" className="btn-primary">
                  Get Started
                </a>
              </div>
            </nav>
          </div>
        )}
      </div>
    </header>
  );
};
